import { useState } from "react";
import { store } from "../../../core/store/store";
import { useNavigate } from "react-router";

export const Confirm = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const formInfo = store.getState().formInfoSlice;
  const displayList = [
    { dt: "", dd: formInfo.name },
    { dt: "", dd: formInfo.age || "-" },
    { dt: "", dd: formInfo.gender },
    { dt: "", dd: formInfo.tel || "-" },
    { dt: "", dd: formInfo.email },
    { dt: "", dd: "" },
    {
      dt: "1",
      dd: formInfo.firstDate + "_" + formInfo.firstTimePeriod,
      subTitle: true,
    },
    {
      dt: "2",
      dd: formInfo.secondDate + "_" + formInfo.secondTimePeriod,
      subTitle: true,
    },
    {
      dt: "3",
      dd: formInfo.thirdDate
        ? formInfo.thirdDate + "_" + formInfo.thirdTimePeriod
        : "-",
      subTitle: true,
    },
    { dt: "", dd: formInfo.text || "-" },
  ];

  const onClick = () => {
    setLoading(true);
    // 
    setTimeout(() => {
      navigate("/contact/complete");
    }, 1000);
  };
  return (
    <>
      <div style={{ maxWidth: "500px", margin: "0 auto", padding: "16px" }}>
        <h2
          style={{
            fontSize: "22px",
            fontWeight: "bold",
            marginBottom: "24px",
            textAlign: "center",
          }}
        >
          
        </h2>
        <dl>
          {displayList.map((v) =>
            v.dd === "" ? (
              <div style={{ padding: "1rem  0 0 0" }}>
                <dt style={{ fontWeight: "bold", fontSize: "18px" }}>{v.dt}</dt>
              </div>
            ) : v.subTitle ? (
              <div
                style={{ borderBottom: "1px solid #ddd", padding: "1rem 0" }}
              >
                <dt
                  style={{
                    fontWeight: "bold",
                    fontSize: "16px",
                    marginLeft: "10px",
                  }}
                >
                  {v.dt}
                </dt>
                <dd style={{ fontSize: "18px", marginTop: "1%" }}>{v.dd}</dd>
              </div>
            ) : (
              <div
                style={{ borderBottom: "1px solid #ddd", padding: "1rem 0" }}
              >
                <dt style={{ fontWeight: "bold", fontSize: "18px" }}>{v.dt}</dt>
                <dd style={{ fontSize: "18px", marginTop: "1%" }}>{v.dd}</dd>
              </div>
            )
          )}
        </dl>

        <button
          style={{
            width: "100%",
            marginTop: "10px",
            padding: "14px",
            fontSize: "16px",
            fontWeight: "bold",
            border: "none",
            borderRadius: "6px",
            backgroundColor: "#eee",
            color: "#333",
            cursor: "pointer",
          }}
          onClick={() => navigate("/contact/form")}
          disabled={loading}
        >
          
        </button>
        <button
          style={{
            width: "100%",
            marginTop: "10px",
            padding: "14px",
            fontSize: "16px",
            fontWeight: "bold",
            border: "none",
            borderRadius: "6px",
            backgroundColor: "#000",
            color: "#fff",
            cursor: "pointer",
          }}
          onClick={onClick}
          disabled={loading}
        >
          
        </button>
      </div>

      <footer
        style={{
          textAlign: "center",
          padding: "1rem",
          fontSize: "0.9rem",
          marginTop: "1%",
          color: "gray",
        }}
      >
        &copy; 2025 Okonomiyaki Gym
      </footer>
    </>
  );
};
