import { useEffect, useState } from "react";
import { store } from "../../../core/store/store";
import { FormInfo, resetFormInfo } from "../../../core/store/form-info/slice";
import { useNavigate } from "react-router-dom";

export const Complete = () => {
  const [info, setInfo] = useState<FormInfo>();
  const navigate = useNavigate();
  useEffect(() => {
    setInfo(store.getState().formInfoSlice);
  }, []);
  return (
    <>
      {info && Object.values(info).map((v) => <p>{v || "-"}</p>)}
      <button
        onClick={() => {
          store.dispatch(resetFormInfo());
          navigate("/contact/form");
        }}
      >
        TOP
      </button>
      <footer
        style={{
          textAlign: "center",
          padding: "1rem",
          fontSize: "0.9rem",
          marginTop: "1%",
          color: "gray",
        }}
      >
        &copy; 2025 Okonomiyaki Gym
      </footer>
    </>
  );
};
